#sec-3 {
  width: 100%;
  height: 100vh;
}
#sec-3 .container {
  height: 100vh;
  margin-left: 560px;
  display: flex;
  justify-content: center;
  align-items: center;
}
#sec-3 .container .box {
  background: linear-gradient(180deg, var(--glassBg30) 0%, var(--glassBg0) 100%);
  width: 500px;
  height: 440px;
  padding: 2rem;
  display: flex;
  flex-direction: column;
}
#sec-3 .container .box .headerBody {
  width: 100%;
  height: 20%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
#sec-3 .container .box .headerBody .headerBox {
  margin-top: -60px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#sec-3 .container .box .headerBody .headerBox .CircleHeader {
  background-color: var(--white);
  width: 80px;
  height: 80px;
  border-radius: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.4), 2px 2px 5px rgba(0, 0, 0, 0.4);
}
#sec-3 .container .box .headerBody .headerBox .CircleHeader .navIcon {
  font-size: 2.4rem;
  color: var(--red1);
  filter: drop-shadow(0px 2px 1px rgba(0, 0, 0, 0.4));
}
#sec-3 .container .box .headerBody .headerBox h2 {
  margin-top: 10px;
  color: var(--white);
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5019607843);
}
#sec-3 .container .box .mainBody {
  box-sizing: border-box;
  width: 100%;
  height: 80%;
  padding-top: 20px;
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: var(--glassBg50);
}
#sec-3 .container .box .mainBody .mainTextBox {
  width: auto;
  margin: 0px 20px;
  margin-top: -20px;
}
#sec-3 .container .box .mainBody .mainTextBox p {
  font-size: 0.8rem;
  color: var(--text);
  text-align: center;
  word-spacing: 1px;
}
#sec-3 .container .box .mainBody .mainTextBox .buttonContent {
  display: flex;
  justify-content: center;
}
#sec-3 .container .box .mainBody .mainTextBox .buttonContent .curriculoButton {
  width: 200px;
  margin: 0px auto;
  margin-top: 40px;
  color: var(--orange);
  font-weight: 500;
  background-color: rgba(255, 255, 255, 0.5882352941);
  border: none;
  padding: 6px;
  transition: 0.3s;
  border-radius: 6px;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.4);
  cursor: pointer;
}
#sec-3 .container .box .mainBody .mainTextBox .buttonContent .curriculoButton .buttonIcon {
  font-size: 1rem;
  margin-right: 10px;
}
#sec-3 .container .box .mainBody .mainTextBox .buttonContent .curriculoButton:hover {
  color: var(--red);
}
#sec-3 .container .box main::-webkit-scrollbar {
  width: 6px;
}
#sec-3 .container .box main::-webkit-scrollbar-track {
  background: transparent;
  margin-top: 20px;
}
#sec-3 .container .box main::-webkit-scrollbar-thumb {
  background-color: var(--glassBg30);
  border-radius: 20px;
  border-top: 2px solid rgba(255, 255, 255, 0.5);
  border-left: 2px solid rgba(255, 255, 255, 0.5);
  border-bottom: 2px solid rgba(0, 0, 0, 0.4);
  border-right: 2px solid rgba(0, 0, 0, 0.4);
}

