.darkScreen {
    background-color: #00000099;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2;
    visibility: hidden;
    opacity: 0;
    transition: 0.5s;

    .modalContent {
        background-color: var(--white);
        width: 700px;
        height: 400px;
        display: flex;
        border-radius: 6px;
        box-shadow: 1px 1px 2px #00000066, 2px 2px 5px #00000066;

        .modalSec45 {
            width: 45%;
            height: 100%;
            position: relative;

            .frame1 {
                width: 100%;
                height: 100.2%;
                border-top-left-radius: 6px;
                border-bottom-left-radius: 6px;
                clip-path: polygon(0 0, 100% 0%, 82% 100%, 0% 100%);
                position: absolute;
                z-index: 2;
            }

            .frame2 {
                width: 100%;
                height: 100.2%;
                border-top-left-radius: 6px;
                border-bottom-left-radius: 6px;
                clip-path: polygon(60% 0, 0% 100%, 92% 100%);
                position: absolute;
                z-index: 1;
            }

            img {
                width: 100%;
                height: 100.2%;
                border-top-left-radius: 6px;
                border-bottom-left-radius: 6px;
                clip-path: polygon(0 0, 100% 0%, 75% 100%, 0% 100%);
                -webkit-touch-callout: none;
                -webkit-user-select: none;
                -khtml-user-select: none;
                -moz-user-select: none;
                -ms-user-select: none;
                user-select: none;
                position: relative;
                z-index: 3;
            }

            .desktop {
                display: block;
            }

            .mobile {
                display: none;
            }
        }

        .modalSec55 {
            box-sizing: border-box;
            width: 55%;
            height: 100%;
            padding: 40px;
            display: flex;
            flex-direction: column;

            .modalHeader {
                width: 100%;
                height: 15%;
                margin: 0;
                padding: 0;
                display: flex;
                flex-direction: row;
                align-items: flex-start;
                justify-content: space-between;

                .modalTitle {
                    font-size: 1.2rem;
                    color: var(--title);
                }

                .closeButton {
                    width: 30px;
                    height: 30px;
                    display: flex;
                    justify-content: flex-end;
                    align-items: center;
                    font-size: 1.2rem;
                    color: var(--title);
                    transition: 0.3s;
                    background-color: transparent;
                    border: none;
                    cursor: pointer;
                }

                .closeButton:hover {
                    color: var(--red1);
                }
            }

            .modalMain {
                width: 100%;
                height: 85%;
                display: flex;
                flex-direction: column;
                overflow-y: auto;
                scrollbar-width: thin;
                scrollbar-color: var(--text);
            }
        }
    }
}

.openModal {
    visibility: visible;
    opacity: 1;
}

@media only screen and (max-width: 800px) {
    .darkScreen {
        .modalContent {
            background-color: var(--white);
            width: 90%;
            height: calc(90vh - 100px);
            margin-bottom: 100px;
            display: grid;
            grid-template-rows: auto minmax(100px, 1fr);
            border-radius: 6px;
            box-shadow: 1px 1px 2px #00000066, 2px 2px 5px #00000066;

            .modalSec45 {
                width: 100%;
                height: 100%;
                position: relative;
    
                .frame1 {
                    width: 100%;
                    height: 100%;
                    border-top-left-radius: 6px;
                    border-top-right-radius: 6px;
                    border-bottom-left-radius: 0px;
                    clip-path: polygon(100% 0, 0 46%, 100% 100%);
                    position: absolute;
                    z-index: 1;
                }

                .frame2 {
                    width: 100%;
                    height: 100%;
                    border-top-left-radius: 6px;
                    border-top-right-radius: 6px;
                    border-bottom-left-radius: 0px;
                    clip-path: polygon(0 0, 100% 0, 100% 85%, 0% 100%);
                    position: absolute;
                    z-index: 2;
                }

                img {
                    width: 100%;
                    height: auto;
                    max-height: 250px;
                    border-top-left-radius: 6px;
                    border-top-right-radius: 6px;
                    border-bottom-left-radius: 0px;
                    clip-path: polygon(0 0, 100% 0, 100% 75%, 0% 100%);
                    -webkit-touch-callout: none;
                    -webkit-user-select: none;
                    -khtml-user-select: none;
                    -moz-user-select: none;
                    -ms-user-select: none;
                    user-select: none;
                    position:relative;
                    z-index: 3;
                }

                .desktop {
                    display: none;
                }

                .mobile {
                    display: block;
                }
            }

            .modalSec55 {
                box-sizing: border-box;
                width: 100%;
                padding: 30px;
                display: flex;
                flex-direction: column;

                .modalHeader {
                    width: 100%;
                    height: 40px;
                    margin: 0;
                    padding: 0;
                    display: flex;
                    flex-direction: row;
                    align-items: flex-start;
                    justify-content: space-between;

                    .modalTitle {
                        font-size: 1.2rem;
                        color: var(--title);
                    }

                    .closeButton {
                        width: 30px;
                        height: 30px;
                        display: flex;
                        justify-content: flex-end;
                        align-items: center;
                        font-size: 1.2rem;
                        color: var(--title);
                        background-color: transparent;
                        border: none;
                        cursor: pointer;
                    }
                }

                .modalMain {
                    width: 100%;
                    height: calc(100% - 40px);
                    display: flex;
                    flex-direction: column;
                }
            }
        }
    }
}

@media only screen and (max-width: 400px) {
    .darkScreen {
        .modalContent {
            .modalSec55 {
                box-sizing: border-box;
                width: 100%;
                padding: 20px;
                display: flex;
                flex-direction: column;

                .modalHeader {
                    width: 100%;
                    height: 40px;
                    margin: 0;
                    padding: 0;
                    display: flex;
                    flex-direction: row;
                    align-items: flex-start;
                    justify-content: space-between;

                    .modalTitle {
                        font-size: 1.2rem;
                        color: var(--title);
                    }

                    .closeButton {
                        width: 30px;
                        height: 30px;
                        display: flex;
                        justify-content: flex-end;
                        align-items: center;
                        font-size: 1.2rem;
                        color: var(--title);
                        background-color: transparent;
                        border: none;
                        cursor: pointer;
                    }
                }

                .modalMain {
                    width: 100%;
                    height: calc(100% - 40px);
                    display: flex;
                    flex-direction: column;
                }
            }
        }
    }
}
