.itemContainer {
  background-color: var(--glassBg20);
  box-sizing: border-box;
  width: 96%;
  height: 80px;
  margin: 0% 1%;
  margin-right: 3%;
  padding: 10px;
  display: flex;
  margin-bottom: 30px;
  border-radius: 10px;
  border-bottom-left-radius: 50px;
  justify-content: flex-start;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.4), 2px 2px 5px rgba(0, 0, 0, 0.4), -1px -1px 2px #ffffff;
}
.itemContainer .circleItem {
  background-color: var(--white);
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  align-items: center;
  position: relative;
  transform: translate(10px, -30px);
  z-index: 1;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.4), 2px 2px 5px rgba(0, 0, 0, 0.4);
}
.itemContainer .circleItem svg {
  position: relative;
  width: 80px;
  height: 80px;
}
.itemContainer .circleItem svg circle {
  width: 40px;
  height: 40px;
  fill: none;
  stroke-width: 10;
  stroke: var(--red);
  top: 50%;
  left: 50%;
  transform: translate(10px, 10px);
  stroke-linecap: round;
  stroke-dasharray: 185;
}
.itemContainer .circleItem svg circle:nth-child(1) {
  stroke: #d9d9d9;
  stroke-dasharray: 185;
}
.itemContainer .circleItem .number {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: var(--title);
  font-size: 0.6rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.itemContainer .circleItem .ImageIcon {
  width: 50px;
  height: 50px;
  margin: 15px;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}
.itemContainer .itemBox {
  margin-left: 30px;
  width: 100%;
  display: flex;
  flex-direction: row;
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: var(--glassBg50);
}
.itemContainer .itemBox .textContent {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.itemContainer .itemBox .textContent .titleBox {
  height: 25px;
  width: 100%;
  margin: 0;
  margin-bottom: 5px;
  display: flex;
  justify-content: space-between;
  line-height: 25px;
}
.itemContainer .itemBox .textContent .titleBox .titulo {
  color: #ef8347;
  font-size: 0.9rem;
  font-weight: 700;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5019607843);
  margin-bottom: 0.8rem;
}
.itemContainer .itemBox .textContent .titleBox button {
  background-color: var(--red);
  width: 25px;
  height: 25px;
  margin-right: 2px;
  color: var(--white);
  font-size: 0.8rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.4), 2px 2px 5px rgba(0, 0, 0, 0.4);
}
.itemContainer .itemBox .textContent p {
  color: var(--text);
  font-weight: 500;
  font-size: 0.8rem;
}
.itemContainer .itemBox::-webkit-scrollbar {
  width: 6px;
}
.itemContainer .itemBox::-webkit-scrollbar-track {
  background: transparent;
}
.itemContainer .itemBox::-webkit-scrollbar-thumb {
  background-color: var(--glassBg30);
  border-radius: 20px;
  border-top: 2px solid rgba(255, 255, 255, 0.5);
  border-left: 2px solid rgba(255, 255, 255, 0.5);
  border-bottom: 2px solid rgba(0, 0, 0, 0.4);
  border-right: 2px solid rgba(0, 0, 0, 0.4);
}

.itemContainer:last-child {
  margin-bottom: 5px;
}

